// App-specific variables
// =========================
$purlContribute_detailsH2Color: $brand_color_white !default;
$purlContribute_detailsTextColor: $brand_color_white !default;
$purlContribute_detailsLinkColor: $brand_color_white !default;
$purlContribute_detailsLinkHoverColor: $brand_color_white !default;
$purlContribute_detailsLinkIconColor: false !default;
$purlContribute_detailsLinkIconHoverColor: false !default;
$purlContribute_detailsMutedTextColor: false !default;

// these styles should sync up with the "purl item" styles in _publicRecognition.scss (unless otherwise noted by Creative)
$purlContribute_detailsBgColor: $brand_color_black !default;
$purlContribute_detailsBgGradType: false !default;
$purlContribute_detailsBgGradMeta: false !default;
$purlContribute_detailsBgGradStops: false !default;
$purlContribute_detailsBgImg: "publicRecognition/pastCelebrationBg.png" !default;
$purlContribute_detailsBgImgPath: $appImagesPath !default;
$purlContribute_detailsBgImgPosition: center center !default;
$purlContribute_detailsBorderTopBottomWidth: 0 !default;
$purlContribute_detailsBorderTopBottomColor: $g5ModPgBorderColor !default;
$purlContribute_detailsBoxShadow: none !default;

$purlContribute_avatarSize: 60px !default;

$purlContribute_contributeBgColor: false !default; // gets .page-topper background by default
$purlContribute_contributeCommentBgColor: $brand_color_white !default;
$purlContribute_contributeNameBorderColor: scale-color($purlContribute_contributeBgColor or $brand_color_white, $lightness: -12.5%) !default;

$purlContribute_commentBgColorOdd: $brand_color_lighter !default;
$purlContribute_commentBgColorEven: false !default;

$purlContribute_commentBorderColorOdd: scale-color($purlContribute_commentBgColorOdd or $brand_color_white, $lightness: -12.5%) !default;
$purlContribute_commentBorderColorEven: scale-color($purlContribute_commentBgColorEven or $brand_color_white, $lightness: -12.5%) !default;

$purlMediaWidth : 432px;
// App-specific code
// ==================================================

/* ----- MODULE STYLES ----- */
.module.purlContribute {

} /* /.module.purlContribute */


/* purlTermsPageView */
#purlTermsPageView {
    .termsBlock {
        height: 320px;
        overflow: auto;
    }

    .purlContributeTermsRadios {
        label {
            font-weight: bold;
        }
    }
}


/* purlContrubutePage */
#PURLContributeListView {
    .purlContributers span {
        display: block;
    }

    thead {
        white-space: nowrap;
    }

    .PURLContributeListTable td {
        vertical-align: middle;
    }
}


/* .purlPage (new purl page scss) */
.purlPage {

    .upSpin {
        display: inline-block;
        @include ie7-inline-block();
        width: 28px;
        height: 28px;
    }

    // initial contributor modal
    #purlInitialModal {
        @include avatarSizing(72px);
        .avatarwrap{
            margin-left: 0;
            margin-bottom: $baseLineHeight;
        }
    }

    // outer container of contribute section and details section
    .contributeToPurlSection,
    .purlDetailsSection {
        .well {
            margin-bottom: 0;
        }
    }

    // comment stuffz
    .contribTopControls {
        margin-top: $baseLineHeight;
        @include clearfix();
    }

    .translateLinkDisabled {
        color: #999999;
    }

    // responsive embedded video
    // see: http://webdesignerwall.com/tutorials/css-elastic-videos
    .videoWrapper {
        position: relative;
        width: $purlMediaWidth;
        max-width: 100%;
        // overflow: hidden;

        .responsiveVideoContainer {
            position: relative;
            padding-bottom: 56.25%;
            // padding-top: 30px;
            height: 0;
            overflow: hidden;

            video,
            iframe,
            object,
            embed {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }

    // video positioning fix for ie7 -- remove this and try to add a comment w/media
    // notice the main video and first comment video not move down
    // http://stackoverflow.com/questions/2473171/ie7-relative-absolute-positioning-bug-with-dynamically-modified-page-content
    .lt-ie8 & /* & is .purlPage */ {
        overflow: hidden;
    }

    .contributeToPurlSection {
        .page-topper {
            @if $purlContribute_contributeBgColor {
                background: $purlContribute_contributeBgColor;
            }
        }
    }

    .contribCommentWrapper {
        position: relative;
        margin-top: $baseLineHeight;
        // margin-right: -1 * ($baseLineHeight / 2);
        // margin-left: -1 * ($baseLineHeight / 2);
        padding: $baseLineHeight;
        background: $purlContribute_contributeCommentBgColor;
        @include clearfix();

        .mask {
            position: absolute;
            z-index: 100;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: $purlContribute_contributeCommentBgColor;
            @include opacity(.5);
        }

        .spin {
            position: absolute;
            top: 50%;
            left: 50%;
            width: $spinner_widthHeight;
            height: $spinner_widthHeight;
            margin: (-1 * $spinner_widthHeight / 2) 0 0 (-1 * $spinner_widthHeight / 2);
        }

        .indent {
            margin-left: $purlContribute_avatarSize * 1.25;
            padding-left: $purlContribute_avatarSize * 0.25;
            border-left: 1px solid $purlContribute_contributeNameBorderColor;
            @include clearfix();
        }

        @include avatarSizing($purlContribute_avatarSize);

        .contribAvatar {
            float: left;
        }

        .commentWrapper {
            // width: 75%;
            // float: left;
        }

        .contribName {
            display: inline;
            float: left;
            font-weight: $fontWeightBold;
            min-height: $baseLineHeight;
            margin-bottom: 4px;
        }

        .commentTools {
            float: right;
            margin-bottom: ($baseLineHeight / 4);
            text-align: right;
            font-size: $fontSizeSmall;

            .remChars {
                font-weight: $fontWeightBold;
            }
        }

        .commentFauxInput {
            clear: right;
            margin: ($baseLineHeight / 4) 0 ($baseLineHeight / 2) 0;
            padding: 0;
            background: $inputBackground;
            border: $formFieldBorderWidth solid $inputBorder;
            @include border-radius($inputBorderRadius);
            @include clearfix();

            &:focus {
                // styles borred from bootstrap/_forms.scss
                border-color: rgba(82,168,236,.8);
                outline: 0;
                outline: thin dotted \9; /* IE6-9 */
                @include box-shadow(inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(82,168,236,.6));
            }

            &.disabled {
                background: $brand_color_lighter;
            }
        }

        .contribCommentInp {
            margin-bottom: 0;
            width: 100%;
            border-width: 0;
            border-bottom: 1px dashed $brand_color_border;
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
            box-sizing: border-box;

            &:focus {
                outline: 0;
                @include box-shadow(none);
            }
        }

        .contribCommentBadWords {
            .badwordsWrapper {
                top: 0;
            }
            .badwordsContent:not(:empty) {
                padding: $formFieldPadding;
            }
        }

        .uploadWrapper {
            float: left;
            position: relative;
            padding: $formFieldPadding;

            .uploadInput {
                position: absolute;
                left: 0;
                width: 100%;
                top: 0;
                height: 100%;
                opacity: 0;
                cursor: pointer;
                z-index: 3;
            }

            .uploadBtn {
                padding: nth($formFieldPadding, 1) (nth($formFieldPadding, 2) * 2);
                font-size: $fontSizeExtraLarge;
                font-weight: $fontWeightBold;

                i {
                    font-weight: inherit;

                    & + i {
                        margin-left: nth($formFieldPadding, 2);
                    }
                }
            }

            .upSpin {
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                z-index: 1;
            }
        }

        /*
        .attachTools {
            a { // tab styles
                padding: 2px 8px;
                display: inline-block;
                @include ie7-inline-block();
                color: $purlContribute_contributeAttachTabTextColor;

                &:hover,
                &:focus {
                    text-decoration: none;
                }
            } // tab stylings

            &.photoMode .attachPhotoBtn,
            &.videoMode .attachVideoBtn {
                background: $purlContribute_contributeAttachBgColor;
            }

            &.photoMode .attachPhotoBtn,
            &.photoMode .attachPhotoBtn i,
            &.videoMode .attachVideoBtn,
            &.videoMode .attachVideoBtn i {
                cursor: default;
            }

            .muted {
                @if $purlContribute_contributeAttachMutedTextColor {
                    color: $purlContribute_contributeAttachMutedTextColor;
                }
            }
        }

        .attachPhotoWrapper,
        .attachVideoWrapper {
            padding: 4px 8px;
            background: $purlContribute_contributeAttachBgColor;

            .closeAttModeBtn {
                margin-top: 4px;
                font-size: 20px;

                // removed the circledAwesomeIcon mixin

                &:hover {
                    cursor: pointer;
                }
            }
        }
        */

        .attachedMediaDisplayWrapper {
            position: relative;
            z-index: 4;
            width: $purlMediaWidth;
            .cmtImgWrap {
                position: relative;
                //max-width: 200px;


                img,
                video {
                    width: 100%;
                }
            }
        }

        // set responsive default width for videos
        .videoWrapper {
            width: $purlMediaWidth;
        }


        // rmBtn (button for removing attached video/image)
        .rmBtn {
            cursor: pointer;
        }

        .videoWrapper,
        .cmtImgWrap {
            // special style for remove btn in these elements
            .rmBtn {
                z-index: 10;
                position: absolute;
                right: 4px;
                bottom: 4px;
            }
        }

        @media (max-width: $breakpoint_mobileMini_max) {
            .contribAvatar {
                position: absolute;
                width: $purlContribute_avatarSize / 2;
                height: $purlContribute_avatarSize / 2;
            }

            .indent {
                margin-left: 0;
                padding-left: 0;
                border-left: 0;
            }

            .contribName {
                display: block;
                float: none;
                margin-left: $purlContribute_avatarSize / 2 * 1.25;
                margin-bottom: 0;
                line-height: $purlContribute_avatarSize / 2;
                border-bottom: 1px solid $purlContribute_contributeNameBorderColor;
            }

            .commentTools {
                clear: both;
                line-height: $purlContribute_avatarSize / 2;
            }

            // .commentWrapper,
            // .uploadWrapper {
            //     width: 100%;
            // }

            // .uploadWrapper {
            //     // padding: 10px 0;

            //     // .uploadBtn {
            //     //     display: none;

            //     //     &-mobile {
            //     //         img {
            //     //             height: 30px;
            //     //             cursor: pointer;
            //     //         }

            //     //         &.ready {
            //     //             display: inline-block;
            //     //             width: 100%;
            //     //         }
            //     //     }
            //     // }

            //     // .uploadInput {
            //     //     top: 10px;
            //     //     left: 0px;
            //     //     width: 100%;
            //     //     height: 30px;
            //     //     display: none;

            //     //     &.ready {
            //     //         display: block;
            //     //     }
            //     // }

            //     // .upSpin {
            //     //     position: relative;
            //     //     left: 0;
            //     //     top: 0;
            //     // }
            // }

            // .attachedMediaDisplayWrapper {
            //     position: relative;
            //     top: 0;
            // }
        }
    }// .contribCommentWrapper


    .purlDetailsSection {
        .page-topper {
            @include page_containerSplitterDisplay(
                $h2Color :              $purlContribute_detailsH2Color,
                $textColor :            $purlContribute_detailsTextColor,
                $linkColor :            $purlContribute_detailsLinkColor,
                $linkHoverColor :       $purlContribute_detailsLinkHoverColor,
                $linkIconColor :        $purlContribute_detailsLinkIconColor,
                $linkIconHoverColor :   $purlContribute_detailsLinkIconHoverColor,
                $borderTopBottomWidth : $purlContribute_detailsBorderTopBottomWidth,
                $borderTopBottomColor : $purlContribute_detailsBorderTopBottomColor,
                $boxShadow :            $purlContribute_detailsBoxShadow
            );

            @include safeImgUrls($purlContribute_detailsBgImg, $purlContribute_detailsBgImgPath, $property: 'background-image');
            background-color: $purlContribute_detailsBgColor;
            background-position: $purlContribute_detailsBgImgPosition;

            // never have a top border on this box
            border-top: 0;
        }

        h4 {
            margin-top: 0;
        }
    }

    .purlDetailsWrapper {
        .purlVideoWrapper {
            width: $purlMediaWidth;
            margin: 0 auto;
            margin-bottom: $baseLineHeight;
        }

        .purlTextWrapper {
            float: none;
            width: auto;
        }

        @media (min-width: $breakpoint_tablet_min) {
            .purlVideoWrapper {

            }
            .purlTextWrapper {
                margin-left: $purlMediaWidth + $g6PagePaddingX_tablet / 2;
            }
        }
        @media (min-width: $breakpoint_desktop_min) {
            .purlVideoWrapper {
                width: $purlMediaWidth;
            }
            .purlTextWrapper {
                margin-left: $purlMediaWidth + $g6PagePaddingX_desktop / 2;
            }
        }
        @media (min-width: $breakpoint_desktopLarge_min) {
            .purlVideoWrapper {
                width: $purlMediaWidth;
            }
            .purlTextWrapper {
                margin-left: $purlMediaWidth + $g6PagePaddingX_desktopLarge / 2;
            }
        }


        .purlMainImage {
            width: 100%;
        }

        .purlDetailText {
            // good values depend on video height + height of purlDetailsRecipientControls
            // @media (min-width: 1200px) {
            //     min-height: 120px;
            // }

            // @media (min-width: 980px) and (max-width: 1199px) {
            //     min-height: 120px;
            // }

            // @media (min-width: 768px) and (max-width: 979px) {
            //     min-height: 60px;
            // }

            // @media (max-width: 767px) {
            //     margin-top: 10px;
            // }
        }

        .expirationNote {
            @if purlContribute_detailsMutedTextColor {
                color: purlContribute_detailsMutedTextColor;
            }
        }

        .share-print {
            float: none;
            margin-left: 0;
            text-align: left;

            @media (min-width: 1200px),
                   (min-width: 480px) and (max-width: 767px) {
                float: right;
                margin: 10px 0 10px 25px;
                text-align: right;
            }
            // @media (max-width: 979px) and (min-width: 768px),
            //        (max-width: 479px) {
            // }
        }

        .btn {
            margin-bottom: $baseLineHeight / 2;
            margin-right: 10px;

            & + .btn {
                margin-left: 0;
            }
        }
    }// .purlDetailsWrapper

    .commentsListWrapper {
        // margin-top: $baseLineHeight / 2;
        zoom: 1;

        .spin {
            display: block;
            margin: 0 auto;
            width: $spinner_widthHeight;
            height: $spinner_widthHeight;
        }
    }

    .commentItemWrapper {
        // padding-bottom: $baseLineHeight / 2;
        // padding-top: $baseLineHeight;
        zoom: 1;

        .innerCommentWrapper {
            // margin: (-1 * $baseLineHeight / 2) (-1 * $baseLineHeight / 2) 0;
            padding: $baseLineHeight;
            zoom: 1;
            @if $purlContribute_commentBgColorEven {
                background: $purlContribute_commentBgColorEven;
            }

            &.flashBg {
                background: $linkColor !important;
            }
        }
        @include avatarSizing($purlContribute_avatarSize);
        .avatarImg {
            float: left;
        }

        .userInfo {
            font-weight: bold;
        }

        .indent {
            margin-left: $purlContribute_avatarSize * 1.25;
            padding-left: $purlContribute_avatarSize * 0.25;
            min-height: $purlContribute_avatarSize;
            border-left: 1px solid $purlContribute_commentBorderColorEven;
        }

        .media {
            img {
                // max-width: 100%;
                max-width: $purlMediaWidth;
                width: 100%;

                // fix for ie8 not properly resizing images vertically - https://basecamp.com/1829100/projects/2650983/todos/121904751
                .lt-ie9 & {
                    width: auto;
                }
            }
            // set responsive default width for videos
            .videoWrapper {
                width: $purlMediaWidth;

                & + .videoWrapper,
                & + img {
                    margin-top: $baseLineHeight;
                }
            }
        }

        @media (max-width: $breakpoint_mobileMini_max) {
            @include avatarSizing($purlContribute_avatarSize / 2);

            .avatar {
                margin-right: $purlContribute_avatarSize / 2 * 0.25;
                margin-bottom: 0px;
                width: $purlContribute_avatarSize / 2;
                height: $purlContribute_avatarSize / 2;
            }

            .indent {
                margin-left: 0;
                padding-left: 0;
                border-left: 0;
            }

            .userInfo {
                margin-left: $purlContribute_avatarSize / 2 * 1.25;
                line-height: $purlContribute_avatarSize / 2;
                border-bottom: 1px solid $purlContribute_commentBorderColorEven;
            }

            .author {
                display: block;
                float: none;
            }

            .text {
                clear: both;
                // margin-top: 10px;
                padding-top: 10px;
            }
        }
    }// .commentItemWrapper

    .commentItemWrapper:nth-of-type(2n+1) {
        @if $purlContribute_commentBgColorOdd {
            .innerCommentWrapper {
                background: $purlContribute_commentBgColorOdd;
            }

            .indent,
            .userInfo {
                border-color: $purlContribute_commentBorderColorOdd;
            }
        }
    }


    // invite contribs qtip etc...
    .inviteContribsQTipOuter {
        max-width: 100%;

        #inviteContribsTip {
            width: 440px;
            @media (max-width: 479px) {
                width: 100%;
            }
        }

        .inviteContribsEmailsInp {
            height: 70px;
            @include input-block-level();

            .lt-ie8 & { // ie7
                padding: 0; // cheap and effective
            }
        }

        .parseEmailsControls {
            margin-bottom: 2px;
        }

        .addEmailsToInviteListBtn {
            margin-bottom: 4px;
        }

        .parseEmailsFeedback {
            display: inline-block;
            @include ie7-inline-block();
            vertical-align: top;
            margin-bottom: 0;
            width: 390px;
        }

        .pendingContribItem {
            font-size: 14px;
            line-height: 18px;
            margin-top: 6px;

            .remBtn i{
                color: $grayLight;
                font-size: 1.1em;
            }

            .remBtn:hover {
                cursor: pointer;
                i {
                    color: $gray;
                }
            }
        }

        .pendingContribsControls {
            margin: 6px 0 2px 0;
        }

        .addedContribsTitle {
            border-top: 1px solid #cccccc;
            padding-top: 4px;
            margin-top: 6px;
            font-weight: bold;
            margin-bottom: 2px;
        }

        .addedContribs {
            line-height: 19px; // so labels look good wrapping
        }

        .inviteeResultItem {
            font-size: 14px;
            line-height: 18px;
        }

        .goodRes {
            color: $brand_color_success;
        }

        .neutralRes {
            color: $brand_color_light;
        }

        .badRes {
            color: $brand_color_alert;
        }

        .spincover {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: rgba(255, 255, 255, .5);
        }

        .spin {
            position: absolute;
            top: 50%;
            left: 50%;
            margin: (-1 * $spinner_widthHeight / 2) 0 0 (-1 * $spinner_widthHeight / 2);
            width: $spinner_widthHeight;
            height: $spinner_widthHeight;
        }
    }// .inviteContribsQTipOuter


    #purlThankEveryoneModal {
        .spinner {
            height: $spinner_widthHeight;
            padding: 20px;
        }
    }// #purlThankEveryoneModal

}/* /.purlPage */
